.Header {

  &-brandingText {
    color: $cBlack054;
    display: inline-block;
    font-size: 24px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    transition: color 250ms ease-in-out;

    @include fs-attention {
      color: $cMetallicSeaweed;
    }

    @include fs-min-width($bpMid) {
      font-size: 32px;
      letter-spacing: 4px;
    }
  }
}


