.SiteNav {

  &-links {
    margin: 0;
    padding: 0;
  }
  
  &-item {
    list-style: none;
    
    @include fs-min-width($bpMid) {
      display: inline-block;
      margin-right: 16px;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }

  &-link {
    color: $cBlack054;
    display: inline-block;
    text-decoration: none;
    transition: color 250ms ease-in-out;

    @include fs-attention {
      color: $cMetallicSeaweed;
    }
  }
}


