.SocialNav {

  &-links {
    margin: 0;
    padding: 0;
  }
  
  &-item {
    display: inline-block;
    list-style: none;

    &:first-of-type {
      &:before {
        display: none;
      }
    }

    &:before {
      color: $cBlack012;
      content: "•";
      margin: 0 8px;
    }

    &:last-of-type {
      margin-right: 0;
    }
  }

  &-link {
    color: $cBlack054;
    display: inline-block;
    text-decoration: none;
    transition: color 250ms ease-in-out;

    @include fs-attention {
      color: $cMetallicSeaweed;
    }
  }
}


