.Footer {

  &-inner {
    border-top: 1px solid $cBlack012;
  }

  &-links { 
    margin: 0;
    padding: 0;
  }

  &-item {
    display: inline-block;
    list-style: none;

    &:before {
      color: $cBlack012;
      content: "•";
      margin: 0 8px;
    }

    &:first-of-type {
      &:before {
        display: none;
      }
    }
  }

  &-link {
    color: $cBlack054;
    display: inline-block;
    text-decoration: none;
    text-transform: lowercase;
    transition: color 250ms ease-in-out;

    @include fs-attention {
      color: $cMetallicSeaweed;
    }
  }

  &-copy {
    color: $cBlack054;
    font-size: 14px;
  }
}


